.home {
  width: 100%;
  align-items: center;
  font-family: "Arial", sans-serif;
  color: #242322;
}

.about {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #1c2128;
  color: #f0f0f0;
}

.about h2 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 60px;
  color: #fab505;
  padding-left: 10px;
  padding-right: 10px;
}

.about .prompt {
  width: 40%;
  font-size: 30px;
}
.about .title {
  height: 120px;
}
.prompt svg {
  font-size: 45px;
  margin: 10px;
  color: white;
}
.prompt svg:hover {
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .home {
    margin-bottom: 50px;
  }
  .about h2 {
    font-size: 30px;
  }

  .prompt svg {
    font-size: 30px;
  }
  .about .prompt {
    font-size: 16px;
    width: 80%;
  }
  .about .title {
    height: 70px;
  }

  .skills {
    font-size: 25px;
    text-align: center;
  }
  .skills .list {
    padding: 0px;
    margin: 0px;
  }
  .skills h2 {
    font-size: 25px;
  }
  .item-list {
    margin-bottom: 80px;
    padding-left: 10%;
    padding-right: 10%;
  }
}

body {
  margin:0;
}

.bg {
  animation:slide 3s ease-in-out infinite alternate;
  background-image: linear-gradient(-60deg, #6c3 50%, #09f 50%);
  bottom:0;
  left:-50%;
  opacity:.5;
  position:fixed;
  right:-50%;
  top:0;
  z-index:-1;
}

.bg2 {
  animation-direction:alternate-reverse;
  animation-duration:4s;
}

.bg3 {
  animation-duration:5s;
}

.content {
  background-color:rgba(255,255,255,.8);
  border-radius:.25em;
  box-shadow:0 0 .25em rgba(0,0,0,.25);
  box-sizing:border-box;
  left:50%;
  padding:10vmin;
  position:fixed;
  text-align:center;
  top:50%;
  transform:translate(-50%, -50%);
}

h1 {
  font-family:monospace;
}

@keyframes slide {
  0% {
    transform:translateX(-25%);
  }
  100% {
    transform:translateX(25%);
  }
}